html {
  font-size: 62.5%; }

body {
  background: #FFF;
  font: normal 16px/1.4 Arial;
  font-size: 1.6rem;
  @include smooth-font;
  min-width: $min-width;
  color: black; }

h1:not([class]), .h1 {
  @include f(36,42,100); }

h2:not([class]), .h2 {
  @include f(48); }

h3:not([class]), .h3 {
  @include f(28,24,100); }

h4:not([class]), .h4 {
  @include f(24,36); }

h5:not([class]), .h5 {
  @include f(12,24,100); }

a:not([class]) {
  color: inherit;
  @include trans(color);
  text-decoration: none;
  &:hover {
    color: red; } }

.l {
  max-width: 1040px;
  padding: 0 20px;
  margin: auto;
  box-sizing: border-box; }

.out {
  position: relative;
  overflow: hidden;
  z-index: 0; }

.clearfix {
  &:before,
  &:after {
    content: "";
    display: table; }
  &:after {
    clear: both; } }

.content-wrapper {
  position: relative;
  z-index: 1; }

.align-center {
  text-align: center; }

.align-right {
  text-align: right; }

.uppercase {
  text-transform: uppercase; }

.spacing-100 {
  letter-spacing: 0.1em; }

.btn {
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  z-index: auto;
  background: none;
  border: 1px solid;
  color: inherit;
  height: 48px;
  padding: 0 15px;
  text-align: center;
  @include f(18, false, 20);
  line-height: 46px;
  font-weight: 700;
  text-decoration: none;
  cursor: pointer;
  @include trans;
  user-select: none;
  > span {
    position: relative;
    z-index: 1; }
  &:before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: none;
    transform: scale(0.5);
    opacity: 0;
    visibility: hidden;
    @include trans; }

  &:hover,
  &:focus {
    border-color: #ff0000;
    color: white;
    &:before {
      opacity: 1;
      visibility: visible;
      background: #ff0000;
      transform: scale(1); } }

  &:active {
    border-color: #f10000;
    color: white;
    &:before {
      background: #f10000; } }

  &.is-active {
    border-color: black;
    color: white;
    &:before {
      background: black;
      opacity: 1;
      visibility: visible;
      transform: scale(1); } }

  &[disabled],
  &.is-disabled {
    background: none;
    color: black;
    border-color: black;
    opacity: 0.2;
    cursor: not-allowed;
    &:before {
      display: none; } }

  &_hero {
    min-width: 303px; }

  &_lg {
    border-width: 2px;
    height: 56px;
    line-height: 54px;
    min-width: 335px;
    @include f(16, false, 25);
    text-transform: uppercase; } }

.tooltip {
  display: block;
  position: absolute;
  z-index: 2;
  bottom: 100%;
  left: 0;
  max-width: 260px;
  @include f(12,18,100);
  font-weight: 700;
  line-height: 1.4;
  color: black;
  text-transform: uppercase;
  padding: 7px 20px;
  box-sizing: border-box;
  background: white;
  white-space: nowrap;
  transition: all 0.2s ease;
  transform-origin: center bottom;
  &:before {
    content: "";
    @include arr(14,10,white,tl);
    position: absolute;
    top: 100%;
    left: 50%; }
  &_arr-right {
    transform-origin: right bottom;
    &:before {
      left: auto;
      right: 0;
      @include arr(14,10,white,tr); } }
  &_arr-left {
    transform-origin: left bottom;
    &:before {
      left: 0; } } }

.tile {
  display: block;
  position: relative;
  z-index: 0;
  float: left;
  width: 25%;
  box-sizing: border-box;
  background-size: cover;
  background-position: center;
  color: white;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  &:before {
    content: "";
    display: block;
    padding-bottom: 100%; }
  &:after {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 0, 0, 0.8);
    box-shadow: 0 0 50px rgba(255, 0, 0, 0.6);
    @include trans;
    opacity: 0;
    visibility: hidden;
    transform: translate3d(0,0,0) scale(1.15); }
  &:hover {
    z-index: 1;
    &:after {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0,0,0) scale(1); } }
  &_2x2 {
    width: 50%; }
  &_pull-right {
    float: right; } }

.tile__inner {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px; }

.tile__title {
  display: block;
  @include f(36,42);
  font-weight: 700; }

.tile__subtitle {
  display: block;
  @include f(14);
  font-weight: 700; }

.tile__number {
  position: absolute;
  top: 15px;
  right: 20px; }

.tile__info {
  position: absolute;
  bottom: 15px;
  left: 100%;
  margin-left: -16px;
  transform: rotate(-90deg);
  transform-origin: 0 100%;
  white-space: nowrap; }
